import React, { useEffect } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";
import "react-pro-sidebar/dist/css/styles.css";

import { setCurrentProject } from "actions/project/setCurrentProject";

// import SideNavigation from "elements/SideNavigation";
// import Navigation from "elements/Navigation";
import { getCurrentProjectFromLocalStorage } from "utils/project.util";

const Dashboard = ({ children, setCurrentProject }) => {
  let { projectId } = useParams();

  useEffect(() => {
    let currentProject = getCurrentProjectFromLocalStorage();

    if (currentProject && currentProject._id === projectId) {
      setCurrentProject(currentProject);
    } else setCurrentProject(projectId);
  }, []);

  return (
    // <div className="flex flex-row w-full h-screen">
    //   <SideNavigation />
    //   <div className="flex flex-grow flex-col overflow-y-auto bg-secondary-1">
    //     <Navigation />
    //     <div className="w-full px-4 py-12 mx-auto antialiased font-body">
    <div className="py-12 px-4 min-h-screen bg-secondary-1">{children}</div>
    //     </div>
    //   </div>
    // </div>
  );
};

const mapDispatchToProps = { setCurrentProject };

export default connect(null, mapDispatchToProps)(Dashboard);
