import React from "react";
import Loader from "react-loader-spinner";

import PrimaryButton from "elements/PrimaryButton";

const CustomFormSubmitBtn = ({
  isSubmitting,
  isValid,
  btnText,
  btnColorClass,
  btnDisabledColorClass,
  btnWidthClass,
}) => {
  return (
    <div>
      {isSubmitting ? (
        <PrimaryButton
          className={`${btnWidthClass || "w-full"} cursor-not-allowed ${
            btnDisabledColorClass || "bg-primary-1"
          }`}
          type="button"
          disabled={isSubmitting}
        >
          <Loader
            className="flex items-center justify-center"
            type="ThreeDots"
            color="#FFFFFF"
            secondaryColor="#FFFFFF"
            height={25}
            width={40}
            timeout={0}
          />
        </PrimaryButton>
      ) : (
        <PrimaryButton
          className={`${btnWidthClass || "w-full"} ${
            !isValid
              ? `cursor-not-allowed ${btnDisabledColorClass || "bg-primary-1"}`
              : btnColorClass || "bg-primary-2"
          }`}
          type="submit"
          disabled={!isValid}
        >
          {btnText}
        </PrimaryButton>
      )}
    </div>
  );
};

export default CustomFormSubmitBtn;
