import axios from "utils/axiosInstance.util";
import { getUserFromLocalStorage } from "utils/auth.util";

export default async (info) => {
  let token = getUserFromLocalStorage();

  let url = `/api/publisher/update/password`;

  let { data } = await axios.patch(url, info, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });

  return data;
};
