import React from "react";

const CustomTabItem = ({ id, active, value, onClick }) => {
  function handleClick() {
    onClick(id);
  }

  return (
    <li
      className={`tab-item cursor-pointer ${active ? "-mb-px" : ""} mr-1`}
      id={id}
      onClick={handleClick}
    >
      <div
        className={`bg-white inline-block py-2 px-4 font-semibold ${
          active ? "border-l border-t border-r rounded-t text-blue-700" : ""
        }`}
      >
        {value}
      </div>
    </li>
  );
};

export default CustomTabItem;
