import { UPDATE_USER_PASSWORD, RESET_PASSWORD_UPDATION_DETAILS } from "./types";

// Service
import updateUserPasswordService from "services/api/project/updateUserPassword.service";

export const updateUserPassword = (
  value,
  { setSubmitting, resetForm }
) => async (dispatch) => {
  try {
    dispatch({ type: UPDATE_USER_PASSWORD.TRIGGER });

    dispatch({ type: UPDATE_USER_PASSWORD.REQUEST });

    let res = await updateUserPasswordService(value);

    dispatch({
      type: UPDATE_USER_PASSWORD.SUCCESS,
      payload: res,
    });
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: UPDATE_USER_PASSWORD.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: UPDATE_USER_PASSWORD.FULFIL });
    setSubmitting(false);
    resetForm();
  }
};

export const resetPasswordUpdationDetails = () => (dispatch) => {
  dispatch({ type: RESET_PASSWORD_UPDATION_DETAILS });
};
