import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import { object, string, ref } from "yup";

import CustomFormField from "elements/CustomForm/CustomFormField";
import CustomFormSubmitBtn from "elements/CustomForm/CustomFormSubmitBtn";
import Alert from "elements/Alert";

// Actions
import { updateUserPassword } from "actions/project/updateUserPassword";

const UserChangePasswordSchema = object().shape({
  currentPassword: string().required("Required"),
  newPassword: string()
    .min(6, "Too Short!")
    .max(20, "Too Long!")
    .required("Required"),
  confirmPassword: string().oneOf(
    [ref("newPassword"), null],
    "Passwords must match"
  ),
});

class UserChangePasswordForm extends PureComponent {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(values, { setSubmitting, resetForm }) {
    // setTimeout(() => {
    //   alert(JSON.stringify(values, null, 2));
    //   setSubmitting(false);
    // }, 400);

    this.props.updateUserPassword(values, {
      setSubmitting,
      resetForm: () =>
        resetForm({
          currentPassword: "",
          newPassword: "",
          confirmPassword: "",
        }),
    });
  }

  render() {
    return (
      <div className="UserChangePasswordForm w-1/2 mx-auto p-4 shadow-md bg-white">
        {this.props.userPassword.error &&
          this.props.userPassword.error.message && (
            // <p>{this.props.userPassword.error.message}</p>
            <Alert
              type="error"
              heading={this.props.userPassword.error.message}
            />
          )}

        {this.props.userPassword.data && this.props.userPassword.data.success && (
          // <p>Successfully updated your details.</p>
          <Alert type="success" heading="Successfully updated your details" />
        )}

        <Formik
          initialValues={{
            currentPassword: "",
            newPassword: "",
            confirmPassword: "",
          }}
          validationSchema={UserChangePasswordSchema}
          onSubmit={this.handleSubmit}
        >
          {({ isSubmitting, isValid }) => (
            <Form>
              <CustomFormField
                label="Current Password"
                name="currentPassword"
                type="password"
              />
              <CustomFormField
                label="New Password"
                name="newPassword"
                type="password"
              />
              <CustomFormField
                label="Confirm Password"
                name="confirmPassword"
                type="password"
              />
              <CustomFormSubmitBtn
                isSubmitting={isSubmitting}
                isValid={isValid}
                btnText="Change Password"
                btnColorClass="bg-primary-2"
                btnWidthClass="w-1/2"
              />
            </Form>
          )}
        </Formik>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.user.data && state.user.data.data,
  userPassword: state.profile.updateUserPassword,
});

const mapDispatchToProps = {
  updateUserPassword,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UserChangePasswordForm);
