import { UPDATE_USER_PROFILE, RESET_PROFILE_UPDATION_DETAILS } from "./types";

// Service
import updateUserProfileService from "services/api/project/updateUserProfile.service";
import { LOGIN_USER } from "actions/types";
import { addUserToLocalStorage } from "utils/auth.util";

export const updateUserProfile = (values, setSubmitting) => async (
  dispatch
) => {
  try {
    dispatch({ type: UPDATE_USER_PROFILE.TRIGGER });

    dispatch({ type: UPDATE_USER_PROFILE.REQUEST });

    let res = await updateUserProfileService(values);

    dispatch({
      type: UPDATE_USER_PROFILE.SUCCESS,
      payload: res,
    });

    dispatch({
      type: LOGIN_USER.SUCCESS,
      payload: res,
    });

    addUserToLocalStorage(res.data.accessToken);
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: UPDATE_USER_PROFILE.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: UPDATE_USER_PROFILE.FULFIL });
    setSubmitting(false);
  }
};

export const resetProfileUpdationDetails = () => (dispatch) => {
  dispatch({ type: RESET_PROFILE_UPDATION_DETAILS });
};
