import React from "react";
import { Field, ErrorMessage } from "formik";

const CustomFormField = ({
  name,
  label,
  type,
  placeholder,
  disabled,
  hidden,
}) => {
  return (
    <div className="pb-4">
      {!!label && (
        <label htmlFor={name} className="text-sm block text-gray-500 pb-2">
          {label}
        </label>
      )}
      <Field
        className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
        type={type}
        name={name}
        placeholder={placeholder}
        disabled={disabled}
        hidden={hidden}
      />
      <ErrorMessage
        className="text-red-500 text-sm"
        name={name}
        component="div"
      />
    </div>
  );
};

export default CustomFormField;
