import React, { useState } from "react";
import { useParams } from "react-router-dom";

import Dashboard from "../Dashboard";
import UserProfileForm from "./UserProfileForm";
import UserChangePasswordForm from "./UserChangePasswordForm";
import CustomTab, { CustomTabItem } from "elements/CustomTab";

const UserProfile = () => {
  const [activeTab, setActiveTab] = useState("userProfile");

  function handleTabClick(tabId) {
    setActiveTab(tabId);
  }

  let { projectId } = useParams();

  const getProfile = () => (
    <>
      <CustomTab>
        <CustomTabItem
          id="userProfile"
          active={activeTab === "userProfile"}
          value="User Profile"
          onClick={handleTabClick}
        />
        <CustomTabItem
          id="changePassword"
          active={activeTab === "changePassword"}
          value="Change Password"
          onClick={handleTabClick}
        />
      </CustomTab>

      {activeTab === "userProfile" && <UserProfileForm />}

      {activeTab === "changePassword" && <UserChangePasswordForm />}
    </>
  );

  if (projectId) return <Dashboard>{getProfile()}</Dashboard>;
  return getProfile();
};

export default UserProfile;
