import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import { object, string } from "yup";

import CustomFormField from "elements/CustomForm/CustomFormField";
import CustomFormSubmitBtn from "elements/CustomForm/CustomFormSubmitBtn";
import Alert from "elements/Alert";

// Actions
import { updateUserProfile } from "actions/project/updateUserProfile";

const UserProfileSchema = object().shape({
  firstname: string()
    .min(3, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  lastname: string()
    .min(3, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  email: string().email("Invalid email").required("Required"),
  userId: string().min(24).max(24),
});

class UserProfileForm extends PureComponent {
  constructor(props) {
    super(props);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(values, { setSubmitting }) {
    this.props.updateUserProfile(values, setSubmitting);
  }

  render() {
    let { firstname, lastname, email, id } = this.props.user;

    return (
      <div className="UserProfileForm w-1/2 mx-auto p-4 shadow-md bg-white">
        {this.props.userProfile.error && this.props.userProfile.error.message && (
          // <p>{this.props.userProfile.error.message}</p>
          <Alert type="error" heading={this.props.userProfile.error.message} />
        )}

        {this.props.userProfile.data && this.props.userProfile.data.success && (
          // <p>Successfully updated your details.</p>
          <Alert type="success" heading="Successfully updated your details" />
        )}
        <Formik
          initialValues={{ firstname, lastname, email, userId: id }}
          validationSchema={UserProfileSchema}
          onSubmit={this.handleSubmit}
        >
          {({ isSubmitting, isValid }) => (
            <Form>
              <CustomFormField
                label="First Name"
                name="firstname"
                type="text"
              />
              <CustomFormField label="Last Name" name="lastname" type="text" />
              <CustomFormField label="Email" name="email" type="email" />
              <CustomFormField
                label="User Id"
                name="userId"
                type="text"
                disabled={true}
              />
              <CustomFormSubmitBtn
                isSubmitting={isSubmitting}
                isValid={isValid}
                btnText="Update Profile"
                btnColorClass="bg-primary-2"
                btnWidthClass="w-1/2"
              />
            </Form>
          )}
        </Formik>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.user.data && state.user.data.data,
  userProfile: state.profile.updateUserProfile,
});

const mapDispatchToProps = {
  updateUserProfile,
};

export default connect(mapStateToProps, mapDispatchToProps)(UserProfileForm);
